<template>
  <div class="hello">
    <el-container>
      <el-aside width="45%">
        <br />
        <a>不积跬步无以至千里</a>
        <br />
        <br />
        <img style="overflow:hidden;" src="/static/home/home-4.jpg" />
        <br />
        <br />
        <a>I never got where I thought I wanted to go,
           but I always got a great story.
        </a>
        <br />
      </el-aside>
      <el-main>
        <div class="htmlContent" v-html="htmlContent"></div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      htmlContent: ""
    }
  },
  mounted() {
    this.handle1()
  },
  methods: {
    async handle1() {
        await axios.get("/static/home/1.md").then(res =>{
            this.htmlContent = this.converter.makeHtml(res.data);
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .hello {
    background-color: #c7edcc;
    height: 95%;
    width: 100%;
    position: fixed;
  }

  .el-container {
      height: 100%;
      background-color: #c7edcc;
  }

  .htmlContent {
    height: 100%;
    float: left;
    text-align: left;
    font-size: 18px;
  }

  .el-aside {
    position: fixed;
    margin-left: 0px;
    height: 100%;
    border-color:#98bf21;
    border-right-style:solid;
    line-height: 30px;
  }

  .el-main {
    margin-left: 45%;
    height: 100%;
    line-height: 30px;
  }

  a {
    color: #dd00dd;
  }
</style>



