import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Interview from '@/components/Interview'
import Words from '@/components/Words'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/interview',
      name: 'Interview',
      component: Interview
    },
    {
      path: '/words',
      name: 'Words',
      component: Words
    }
  ]
})



